<template>
  <div>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <v-spacer></v-spacer>
      <div v-text="user"></div>
      <v-btn
        @click="logOut"
        target="_blank"
        text
        v-if="user"
      >
        <span class="mr-2">LogOut</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
      <v-btn
        @click="openLogIn"
        target="_blank"
        text
        v-if="!user"
      >
        <span class="mr-2">LogIN</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
    </v-app-bar>
    <v-main>
      <slot></slot>
    </v-main>
  </div>
</template>

<script>
import { getUserData } from '@/scripts/user.js';
export default {
  name: 'AppLayout',
  components: {
  },
  data: () => ({
    yey: false,
    child_window: null
  }),
  mounted(){
    window.closeChildWindow = function(){
      this.child_window == null;
      window.location.reload();
    }
  },
  computed: {
    user: function () {
      return getUserData();
    }
  },
  methods: {
    openLogIn: function () {
      const w = 400;
      const h = 700;
      const l = (screen.width / 2) - w / 2;
      const t = (screen.height / 2) - h / 2;
      this.child_window = window.open('/api/auth/reddit','Reddit EI','height=' + h +',width=' + w +',left=' + l +',top=' + t + ',toolbar=0,menubar=no,location=no,resizable=off');      
    },
    logOut: function () {
      window.location.href = '/api/logout';
    }
  }
};
</script>
