<template>
  <v-app>
      <router-view/>
  </v-app>
</template>

<script>
import {getUserData} from '@/scripts/user.js';
export default {
  name: 'App',
  created: function(){
    fetch('/api/am_i_in').then(resp => resp.json()).then(json => {
      if(!json.ok && getUserData()){
        window.location.href = '/api/logout';
      }
    })
  }
}
</script>
