<template>
  <div v-if="this.boardArray.length != 0">
    <div>TITLE</div>
    <v-container name="boardList">
      <ul>
        <li v-for="item in boardArray" :key="item._id">{{item.name}}</li>
      </ul>
    </v-container>
  </div>
</template>
<script>
export default {
  data: function(){
    return {
      boardArray: []
    }
  },
  mounted(){
    this.updateBoard();
  },
  methods: {
    updateBoard: function(){
      fetch('/api/get_board').then(resp => resp.json()).then(json => {
        console.log(json);
        this.boardArray = json.board;
      });
    }
  }
}
</script>