<template>
<div class="elevation-3 rounded-lg">
  <div class="blue pa-3 rounded-lg">
    <div class="text-h6">DOLARCITO</div>
  </div>
  <v-container>
    <v-text-field label="Compra" type="number" v-model="compra"></v-text-field>
    <v-text-field label="Venta" type="number" v-model="venta"></v-text-field>
    <v-text-field label="Puntos a apostar" type="number" v-model="pointsToBet"></v-text-field>
    <v-btn @click="this.shout">shout</v-btn>
  </v-container>
</div>
</template>
<script>
export default {
  name: 'BetComp',
  data: function(){
    return {
      compra: '',
      venta: '',
      pointsToBet: ''
    }
  },
  methods: {
    shout: function (){
      fetch('/api/create_bet', {
        headers: {
          'Content-Type': 'application/json'
        },
        method: 'post',
        body: JSON.stringify({
          compra: this.compra,
          venta: this.venta,
          pointsToBet: this.pointsToBet,
        }),
      }).then(response => response.json())
      .then(json => {
        console.log(json);
      });
    }
  }
}
</script>